@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600&display=swap);
.about__container {
    display: flex;
    grid-template-columns: 40% 50%;
    grid-gap: 10%;
    gap: 10%;
}

.about__me {
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    display: grid;
    place-items: center;
}

.about__me-image {
    transition: -webkit-transform 600ms;
    transition: transform 600ms;
    transition: transform 600ms, -webkit-transform 600ms;
    overflow: hidden;
    border-radius: 2rem;
}

.about__me-image:hover {
    -webkit-transform: rotate(-20deg) scale(1.15);
            transform: rotate(-20deg) scale(1.15);
}

.about__cards {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.about__card {
    text-align: center;
    border: 1px solid transparent;
    background: var(--color-lightshade);
    color: var(--color-text);
    border-radius: 1rem;
    padding: 1rem;
    transition: all 500ms ease;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-darkaccent);
}

.about__icon {
    color: var(--color-text);
    font-size: 1.8rem;
    margin-bottom: .5rem;
}

.about__card h5 {
    font-size: 1rem;
}

.about__card small {
    font-size: .75rem;
}

.about__content p {
    margin-top: 2rem;
    margin-bottom: 3rem;
}

p {
    color: var(--color-text);
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    .about__container {
        grid-gap: 5%;
        gap: 5%;
    }
    .about__me-image {
        margin: 0rem;
    }
    .about__me-image:hover {
        -webkit-transform: rotate(-20deg) scale(1.08);
                transform: rotate(-20deg) scale(1.08);
    }
}

@media screen and (max-width: 600px) {
    .about__container {
        display: list-item;
        list-style: none;
    }
    .about__me-image {
        margin: 0rem;
        transition: none;
    }
    .about__me-image:hover {
        -webkit-transform: none;
                transform: none;
    }
    .about__card {
        margin-top: 2rem;
    }
    
}
.contact__container {
    display: grid;
    grid-template-columns: 30% 60%;
    grid-gap: 10%;
    gap: 10%;
}

.contact__options {
    display: flex;
    flex-direction: row;
}

.contact__option {
    background: var(--color-lightshade);
    border-radius: 3rem;
    border: 3px solid transparent;
    padding: 3rem;
    text-align: center;
    transition: var(--transition);
    color: var(--color-text);
}

.contact__option:hover {
    background: transparent;
    border: 3px solid var(--color-darkaccent);
}

.contact__icons {
    font-size: 2rem;
    margin-bottom: .25rem;
}

.contact__option a {
    display: inline-block;
    font-size: 0.9rem;
    color: var(--color-text);
    transition: var(--transition);
    border-radius: 3rem;
    border: 1px solid transparent;
    padding: 1rem;
}

.contact__option a:hover {
    background: var(--color-text);
    color: var(--color-main);
}

.contact__option h4,h5 {
    margin-bottom: .4rem;
}

form {
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

input, textarea {
    padding: 1.2rem;
    border-radius: 1rem;
    border: 3px solid transparent;
    transition: all 300ms ease-in-out;
    resize: none;
    font-family: 'Open Sans', sans-serif;
}

input:hover,textarea:hover {
    border: 3px solid var(--color-darkaccent);
}

.Modal {
    position: absolute;
    padding: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 3rem;
    background: var(--color-main);
    color: var(--color-lightshade);
}

.Modal p {
    margin-bottom: 1rem;
}

.Modal__Overlay {
    position: fixed;
    inset: 0;
    display: grid;
    place-items: center;
    background-color: rgba(0,0,0,.6);

}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    .contact__container {
        grid-template-columns: 1fr;
        grid-gap: 2rem;
        gap: 2rem;

    }
    .contact__options {
        margin: 0 auto;
        padding: 2rem 0rem 0rem 0rem;
        width: 85%;
    }
    .contact__option {
        width: 100%;  
    }
    form {
        margin: 0 auto;
        width: 85%;
    }
}

@media screen and (max-width: 600px) {
    .contact__options {
        margin: 0 auto;
        padding: 0rem;
        width: 100%;
    }
    form {
        margin: 0 auto;
        padding: 0rem;
        width: 100%;
    }
}
.experience__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
}

.experience__container > div {
    background: var(--color-lightshade);
    color: var(--color-text);
    border-radius: 3rem;
    border: 1px solid transparent;
    padding: 3rem;
    transition: var(--transition);
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
}

.experience__container > div:hover {
    background: transparent;
    border: 1px solid var(--color-darkaccent);
}

.experience__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
}

.experience__details {
    display: flex;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    .experience__container {
        display: grid;
        grid-template-columns: 1fr;
    }

    .experience__container > div {
        margin: 0 auto;
        padding: 2rem 0rem 2rem 0rem;
        width: 85%;
    }
}

@media screen and (max-width: 600px) {
    .experience__container {
        grid-gap: 1rem;
        gap: 1rem;
    }
    .experience__container > div {
        padding: 2rem 0rem;
        width: 100%;
    }
}
footer {
    margin-top: 5rem;
    background: var(--color-lightshade);
    padding: 3rem 0rem;
    text-align: center;
    font-size: .9rem;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto 1.5rem;
    grid-gap: 2rem;
    gap: 2rem;
    transition: var(--transition);
}

.permalinks li:hover {
    text-decoration: underline;
}

.footer__socials {
    display: flex;
    justify-content: center;
    grid-gap: 2rem;
    gap: 2rem;
    margin-bottom: 2rem;
}

.footer__socials a {
    background: var(--color-darkaccent);
    color: var(--color-main);
    padding: .7rem .9rem;
    border-radius: .5rem;
    border: 1px solid transparent;
}

.footer__socials a:hover {
    background: var(--color-text);
    padding: .7rem .9rem;
    border-radius: .5rem;
    border-color: var(--color-text);
    color: var(--color-main);
}

.footer__copyright {
    margin-bottom: 4rem;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
}

@media screen and (max-width: 600px) {
    .permalinks {
        flex-direction: column;
        grid-gap: 1rem;
        gap: 1rem;
    }
    .footer__socials {
        display: flex;
        justify-content: center;
        grid-gap: 1rem;
        gap: 1rem;
        margin-bottom: 2rem;
    }
    .footer__copyright {
        margin-bottom: 5rem;
    }
}
header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*CTA styling*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
}

/*Socials*/
.header__socials {
    margin-top: 1rem;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: center;
}

.header__socials a {
    background: var(--color-lightshade);
    color: var(--color-main);
    padding: .7rem .9rem;
    border-radius: .5rem;
    border: 1px solid transparent;
}

.header__socials a:hover {
    background: var(--color-text);
    border-color: var(--color-text);
    color: var(--color-main);
}

/*Scroll down*/
.scroll__down {
    position: absolute;
    right: -3rem;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    header {
        height: 70vh;
    }
}

@media screen and (max-width: 600px) {
    header {
        height: 100vh;
    }

    .scroll__down {
        display: none;
    }
}
nav {
    background: var(--color-main);
    width: -webkit-max-content;
    width: max-content;
    padding: 0.5rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.6rem;
    gap: 0.6rem;
    border-radius: 5rem;
    opacity: .85;
}

nav a {
    background: transparent;
    padding: .6rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-text);
}

nav a.active {
    background: var(--color-lightshade);
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    nav {
        padding: 0.6rem 1.7rem;
        bottom: 2.5rem;
    }
    nav a {
        padding: .5rem;
    }
}

@media screen and (max-width: 600px) {
    nav {
        padding: 0.6rem 1.7rem;
        bottom: 2.5rem;
        max-width: 95%;
    }
    nav a {
        padding: .4rem;
    }
}
.portfolio__container {
    text-align: center;
}

.portfolio__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
}

.portfolio__item {
    padding: 2rem;
    border-radius: 3rem;
    border: 3px solid transparent;
    background: var(--color-lightshade);
    color: var(--color-text);
    transition: var(--transition);
}

.portfolio__item:hover {
    border: 3px solid var(--color-darkaccent);
    background: transparent;
}

.portfolio__item img {
    border-radius: 1rem;
    aspect-ratio: 3/2;
    border: 3px solid transparent;
    transition: var(--transition);
}

.portfolio__item img:hover {
    border-radius: 1rem;
    opacity: .7;
    border: 3px solid var(--color-darkaccent);
}

.portfolio__item h3 {
    margin: 1rem;
}

.stacks__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.frontend__container,.backend__container{
    justify-content: center;
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    font-size: 1.5rem;
    margin: .25rem 0rem;
}

.portfolio__item-cta {
    display: flex;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    margin-top: 1rem;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    .portfolio__items {
        margin: 0 auto;
        width: 85%;
    }
    .stacks__container {
        display: grid;
        grid-template-columns: 1fr;
    }
    
}

/* TEMP FIX for .portfolio__items not responding properly */
@media screen and (max-width: 900px) {
    .portfolio__items {
        grid-template-columns: repeat(1, 1fr);
    }
    
}

@media screen and (max-width: 600px) {
    .portfolio__items {
        width: 100%;
        grid-template-columns: repeat(1, 1fr);
    }
}
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-text: #264143;
    --color-lightshade: #CCD5AE;
    --color-lightaccent: #E9EDC9;
    --color-main: #FEFAE0;
    --color-darkaccent: #887850;
    --color-darkshade: #D4A373;

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Open Sans', sans-serif;
    background: #FEFAE0;
    background: var(--color-main);
    color: #887850;
    color: var(--color-darkaccent);
    background-image: url(/static/media/bright-squares.53c1ec5f.png);
}

/* GENERAL STYLING */
.container {
    width: 75%;
    width: var(--container-width-lg);
    margin: 0 auto;
}

/*
h1,h2,h3,h4,h5 {
    font-weight: 500;
}
*/

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    min-height: 80vh;
}

section > h2 {
    text-align: center;
    color: #887850;
    color: var(--color-darkaccent);
    margin-bottom: 3rem;
}

section > h5 {
    padding-top: 2rem;
    text-align: center;
    color: #887850;
    color: var(--color-darkaccent);
}

a {
    color: #264143;
    color: var(--color-text);
    transition: all 400ms ease;
    transition: var(--transition);
}

a:hover {
    color: #887850;
    color: var(--color-darkaccent);
}

.btn {
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    color: #264143;
    color: var(--color-text);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #264143;
    border: 1px solid var(--color-text);
    transition: all 400ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #CCD5AE;
    background: var(--color-lightshade);
    color: #FEFAE0;
    color: var(--color-main);
    border-color: #D4A373;
    border-color: var(--color-darkshade);
}

.btn-primary {
    background-color: #264143;
    background-color: var(--color-text);
    color: #E9EDC9;
    color: var(--color-lightaccent);
    border: 1px solid transparent;
}

.btn:hover {
    color: #FEFAE0;
    color: var(--color-main);
}

.btn-primary:hover {
    border-color: #887850;
    border-color: var(--color-darkaccent);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1024px) {
    .container {
        width: 86%;
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

@media screen and (max-width: 600px) {
    .container {
        width: 90%;
        width: var(--container-width-sm);
    }

    section > h2 {
        margin-bottom: 2rem;
    }
}
